/** @format */

*::-webkit-scrollbar {
	width: 0;
}
* {
	-ms-overflow-style: none;
}
* {
	overflow: -moz-scrollbars-none;
}

html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

html,
body {
	position: relative;
}

ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
a,
img {
	margin: 0;
	padding: 0;
}

button {
	cursor: pointer;

	&:focus {
		outline: transparent;
	}
}

input:focus,
textarea:focus {
	outline: transparent;
}

fieldset {
	border: none;
	padding: 0;
	margin: 0;
	display: block;
}

body {
	max-width: 100vw;
	background-color: $bgColor;
	color: $text;
	font-family: Inter;
	font-style: normal;
	font-weight: 500;
	font-size: 14px;
	line-height: 24px;
}

.container {
	max-width: 1560px;
	padding: 0 30px;
	width: 100%;
	margin: 0 auto;

	@media screen and (max-width: 560px) {
		padding: 0 15px;
	}
}

.green-btn {
	box-shadow: 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
	background: linear-gradient(179.52deg, #00a942 -13.56%, #208648 158.3%);
	border-radius: 30px;
	border: none;
	transition: all 0.3s;
	display: block;
	padding: 18px 0;
	text-align: center;
	min-width: 255px;
	margin: 20px auto 0;
	transition: all 0.5s;
	display: block;
	border: 1px solid transparent;

	&__text {
		font-family: Ubuntu;
		font-style: normal;
		font-weight: 500;
		font-size: 14px;
		line-height: 16px;
		text-align: center;
		color: white;
	}

	&:hover,
	&:focus {
		background: linear-gradient(188.28deg, #45b64a -0.83%, #588559 100%);
		box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
	}
}

.section__title {
	font-family: Ubuntu;
	font-style: normal;
	font-weight: bold;
	font-size: 24px;
	line-height: 32px;
	color: $title;
	text-align: center;
}

.section__text {
	font-family: Inter;
	font-style: normal;
	font-weight: 500;
	font-size: 14px;
	line-height: 18px;
	text-align: center;
}

.parallax-item {
	position: absolute;
	z-index: -1;
	width: 15%;
	object-fit: contain;

	@media screen and (max-width: 768px) {
		display: none;
	}
}

/* toast Styles start*/

#toast-container {
	z-index: 10;

	> .toast-info {
		border-radius: 0;
		padding: 24px 24px 0 24px;
		background-color: #fff;
		opacity: unset !important;
		min-height: 100px;
		box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);

		&:hover {
			box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
		}

		@media screen and (max-width: 650px) {
			display: none;
		}
	}

	.toast-progress {
		height: 6px;
		background-color: $yellow;
		opacity: 1;
	}

	.toast-close-button {
		color: black !important;
	}

	.toast-message {
		font-size: 11px;
		line-height: 13px;
		color: $darkGreen;
		text-align: center;
		max-width: 200px;
		margin: 0 auto;

		.toast-strong {
			display: block;
			font-weight: 500;
			font-size: 15px;
			line-height: 16px;
			margin-top: 13px;
		}
	}
}

.noscroll {
	overflow-y: hidden !important;
}

/* toast Styles end*/
