/** @format */

@import "vars";
@import "fonts";
@import "global";
//-----------------------------------------------------------------------------------------------------------
//
//                                                    START CODE
//
//-----------------------------------------------------------------------------------------------------------

//STYLES-SECTION - header

.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	margin: 0 auto;
	height: 131px;
	overflow: hidden;
	width: 100%;
	margin-bottom: -131px;
	z-index: 10;
	transition: all 0.7s;

	&__inner {
		border-bottom-left-radius: 50%;
		border-bottom-right-radius: 50%;
		box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
		background-color: $bgColor;
		width: 100%;
		min-width: 800px;
		max-width: 1080px;
		height: 1080px;
		margin: 0 auto;
		position: absolute;
		bottom: 12px;
		left: 0;
		right: 0;
		transition: border-radius 1.5s;

		@media screen and (max-width: 800px) {
			transform: translateX(-50%);
			left: 50%;
			right: unset;
			margin: unset;
		}
	}

	&__link-wrapper {
		height: 100%;
		margin-left: auto;
		margin-right: auto;
		display: flex;
		align-items: flex-end;
	}

	&__link {
		display: block;
		height: 100px;
		margin: 0 auto 1%;
		transition: all 0.7s;
	}

	&--scrolled {
		.header__inner {
			border-radius: unset;
			width: auto;
			height: 90px;
			position: static;
			max-width: unset;

			@media screen and (max-width: 800px) {
				min-width: 100%;
				left: 0;
				right: 0;
				transform: none;
				height: 75px;
			}
		}

		& .header__link {
			height: 75px;

			@media screen and (max-width: 800px) {
				height: 70px;
			}
		}
	}

	&__logo {
		height: 100%;
		object-fit: contain;
		display: block;
	}
}

//!STYLES-SECTION



//STYLES-SECTION - top-section

.top {
	min-height: 100vh;
	padding-bottom: 48px;
	position: relative;

	&__overlay {
		position: absolute;
		z-index: 1;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		background-size: 75%;

		&-1 {
			background-image: url(../img/money-1.png);
			background-position: 0 0;
		}

		&-2 {
			background-image: url(../img/money-2.png);
			background-position: center;
		}

		&-3 {
			background-image: url(../img/money-3.png);
			background-position: bottom right;
		}

		&-mobile {
			background-image: url(../img/p_mobile.png);
			background-size: contain;
			background-position: center top;

			@media screen and (min-width: 769px) {
				display: none;
			}
		}
	}

	&__inner {
		background-image: url(../img/top-bg.svg);
		background-repeat: no-repeat;
		background-position: center bottom;
		background-size: cover;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		text-align: center;
		color: white;
		padding-bottom: 48px;
		position: relative;

		& #scene,
		#scene__mobile {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
		}

		& #scene {
			@media screen and (max-width: 768px) {
				display: none;
			}
		}

		& #scene__mobile {
			@media screen and (min-width: 769px) {
				display: none;
			}
		}

		@media screen and (max-width: 560px) {
			padding-bottom: 0;
			background-size: unset;
			background-position: center top;
		}

		& .container {
			position: relative;
			z-index: 2;
		}
	}

	&-title {
		margin-top: 150px;
		font-family: Ubuntu;
		font-style: normal;
		font-weight: bold;
		font-size: 24px;
		line-height: 32px;
		text-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);

		@media screen and (max-width: 560px) {
			margin-top: 25vh;
		}
	}

	&-text {
		margin-top: 100px;
		font-family: Inter;
		font-style: normal;
		font-weight: 500;
		font-size: 14px;
		line-height: 18px;
		text-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	}

	.calc {
		margin: 48px auto 60px;
		background: linear-gradient(180deg, #ffffff 0%, #e9e9e9 100%);
		border-radius: 30px;
		padding: 25px 30px;
		color: #393939;
		margin-bottom: 60px;
		text-align: center;
		width: 526px;
		position: relative;
		z-index: 9;

		@media screen and (max-width: 560px) {
			width: 100%;
			border-radius: unset;
			margin: 48px 0 0;
		}

		&-form {
			max-width: 466px;
			width: 100%;

			&__title {
				font-family: Ubuntu;
				font-weight: bold;
				font-size: 24px;
				line-height: 32px;
				margin-bottom: 30px;
			}

			&__input,
			&__range {
				display: block;
			}

			&__input {
				display: flex;
				justify-content: space-between;
				align-items: center;

				& input {
					background: #fbfbfb;
					border: 1px solid #ffffff;
					box-sizing: border-box;
					box-shadow: inset 2px 2px 2px rgba(0, 0, 0, 0.05);
					border-radius: 3px;
					max-width: 106px;
					padding: 12px;
					text-align: center;
					font-family: Ubuntu;
					font-style: normal;
					font-weight: 500;
					font-size: 13px;
					line-height: 18px;
					color: $green;
					width: 40%;
				}
			}

			&__days {
				display: block;
				background: #fbfbfb;
				border: 1px solid #ffffff;
				border-radius: 3px;
				text-align: center;
				padding: 15px 0;
				height: 50px;
				margin-right: auto;
				font-family: Ubuntu;
				font-weight: 500;
				font-size: 13px;
				line-height: 18px;
				color: #514e70;
				width: 106px;
			}

			&__text {
				font-family: Ubuntu;
				font-weight: 500;
				font-size: 15px;
				line-height: 16px;
				margin-right: 20px;
			}

			&__range {

				.irs-min,
				.irs-max,
				.irs-single {
					display: none;
				}

				.irs--flat .irs-line {
					top: 25px;
					height: 5px;
					background: #fafafa;
					box-shadow: inset 2px 2px 2px rgba(0, 0, 0, 0.1);
					border-radius: 3px;
				}

				.irs--flat .irs-bar {
					top: 25px;
					height: 5px;
					background: $lightGreen;
					border-radius: 3px;
				}

				.irs--flat .irs-handle {
					top: 13px;
					width: 30px;
					height: 30px;
					background: #f4d64c;
					border: 5px solid $lightGreen;
					box-sizing: border-box;
					box-shadow: -2px 2px 2px rgba(0, 0, 0, 0.05);
					border-radius: 50%;

					& i:first-child {
						height: 20px;
						width: 20px;
						border-radius: 50%;
						top: 50%;
						-webkit-transform: translate(-50%, -50%);
						transform: translate(-50%, -50%);
						margin-left: 0;
						background-color: transparent;
						border: 1px solid #f9eaa5;
						box-shadow: -2px 2px 2px rgba(0, 0, 0, 0.05);
					}

					& i:first-child::before,
					& i:first-child::after {
						content: "";
						position: absolute;
						background: #ffffff;
						opacity: 0.5;
						box-shadow: -1px 0px 0px rgba(0, 0, 0, 0.1);
						height: 8px;
						width: 1px;
						top: 50%;
						-webkit-transform: translateY(-50%);
						transform: translateY(-50%);
					}

					& i:first-child::before {
						left: 7px;
					}

					& i:first-child::after {
						right: 7px;
					}
				}

				&:hover .irs-single {
					display: block;
					background-color: $lightGreen;
				}

				&:hover .irs-single::before {
					border-top-color: $lightGreen;
				}
			}

			&__percent {
				margin-top: 14px;

				&-text {
					font-family: Ubuntu;
					font-weight: 300;
					font-size: 14px;
					line-height: 16px;
					color: $subGrey;
				}
			}

			&__money,
			&__zero {
				margin-left: 20px;
			}

			&__money {
				font-family: Ubuntu, sans-serif;
				margin-left: 20px;
				font-weight: 500;
				font-size: 18px;
				line-height: 21px;
				color: $green;
				min-width: 85px;
				display: inline-block;
			}
		}

		& .green-btn {
			display: block;
			max-width: 160px;
		}
	}

	& .section__title {
		margin-bottom: 48px;
	}

	& .section__text {
		max-width: 1000px;
		margin: 0 auto;
	}

	& &__content {
		position: relative;
		z-index: 2;
		padding-top: 40px;

		&-inner {
			display: flex;
			justify-content: space-between;
			align-items: stretch;
			margin-bottom: 42px;

			@media screen and (max-width: 768px) {
				flex-wrap: wrap;
			}

			@media screen and (max-width: 500px) {
				flex-direction: column;
			}
		}

		&-item {
			max-width: 300px;
			text-align: center;
			padding: 0 10px;

			@media screen and (max-width: 768px) {
				width: 50%;
			}

			@media screen and (max-width: 500px) {
				width: 100%;
				margin: 0 auto;
			}

			.image-box {
				height: 144px;
				width: 144px;
				// background: linear-gradient(
				// 	10.99deg,
				// 	rgba(232, 232, 232, 0.75) 7.57%,
				// 	rgba(255, 255, 255, 0.75) 96.86%
				// );
				// border: 9px solid #ffffff;
				// box-shadow: inset 0px 4px 20px rgba(0, 0, 0, 0.25);
				padding: 12px;
				margin: 0 auto;
				border-radius: 50%;
				margin-bottom: 40px;
				transition: all 0.7s;

				img {
					width: 100%;
					object-fit: contain;
					transition: all 0.7s;
				}

				&:hover {
					transform: scale(1.3);

					img {
						transform: scale(1.6);
					}
				}
			}

			.tex-box {
				font-family: Inter;
				font-weight: 600;
				font-size: 18px;
				line-height: 18px;
			}
		}

		.parallax-item {
			right: 130px;
			bottom: -30px;
		}
	}

	&__content-text {
		font-family: Inter;
		font-style: normal;
		font-weight: 500;
		font-size: 14px;
		line-height: 24px;
		color: #727881;
		padding: 1.5em 0 0;
	}

	&__text-box {
		width: 100%;
		margin: 0 auto;
		max-width: 525px;
		background: #ffffff;
		border-radius: 10px;
		padding: 10px 0;
		color: #384251;
	}

	&__text {
		font-weight: 500;
		font-size: 14px;
		line-height: 32px;
		text-align: center;

		&--strong {
			color: $lightGreen;
			font-weight: 500;
			font-size: 18px;
		}
	}
}

//!STYLES-SECTION



//STYLES-SECTION - steps

.steps {
	background-color: #ffffff;
	counter-reset: steps;
	padding: 50px 0;

	&__inner {
		display: flex;
		margin-top: 60px;

		//MEDIA - 1300
		@media screen and (max-width: 1300px) {
			flex-direction: column-reverse;
		}

		//!MEDIA
	}

	&__title {
		position: relative;
		font-weight: 600;
		font-size: 18px;
		line-height: 1.2;
		color: $title;
		margin-left: 40px;

		&-line {
			transition: all 0.26s;
			outline: 3px solid transparent;
		}

		&::before {
			counter-increment: steps;
			content: counter(steps);
			position: absolute;
			left: -40px;
			bottom: -10px;
			font-weight: 800;
			font-size: 48px;
			line-height: 58px;
			color: $green;
			transition: all 0.26s;
		}
	}

	&__list {
		flex: 1 1 49%;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-template-rows: repeat(6, 1fr);
		grid-column-gap: 10px;

		//MEDIA - 1300
		@media screen and (max-width: 1300px) {
			flex: 0 0 auto;
			max-width: 80%;
			margin-left: auto;
			margin-right: auto;
		}

		//!MEDIA

		//MEDIA - 767
		@media screen and (max-width: 767px) {
			max-width: unset;
		}

		//!MEDIA

		//MEDIA - 459
		@media screen and (max-width: 459px) {
			grid-template-columns: 1fr;
			grid-template-rows: 1fr;

		}

		//!MEDIA
	}

	&__list-item {
		list-style: none;

		//MEDIA - 459
		@media screen and (max-width: 459px) {
			margin-bottom: 32px;
		}

		//!MEDIA

		&--left {
			grid-column: 1;
		}

		&--right {
			grid-column: 2;

			//MEDIA - 459
			@media screen and (max-width: 459px) {
				grid-column: 1;
			}

			//!MEDIA
		}

		&--1 {
			grid-row: 1/3;

			//MEDIA - 459
			@media screen and (max-width: 459px) {
				grid-row: auto;
			}

			//!MEDIA
		}

		&--2 {
			grid-row: 2/4;

			//MEDIA - 459
			@media screen and (max-width: 459px) {
				grid-row: auto;
			}

			//!MEDIA
		}

		&--3 {
			grid-row: 3/5;

			//MEDIA - 459
			@media screen and (max-width: 459px) {
				grid-row: auto;
			}

			//!MEDIA
		}

		&--4 {
			grid-row: 4/6;

			//MEDIA - 459
			@media screen and (max-width: 459px) {
				grid-row: auto;
			}

			//!MEDIA
		}

		&--5 {
			grid-row: 5/6;

			//MEDIA - 459
			@media screen and (max-width: 459px) {
				grid-row: auto;
			}

			//!MEDIA
		}

		&:hover {
			.steps__title-line {
				background-color: #e0eb2a;
				outline: 3px solid #e0eb2a;
			}

			.steps__title::before {
				background: linear-gradient(175.6deg, rgb(0, 169, 66) -13.56%, rgb(32, 134, 72) 158.3%);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
			}
		}
	}

	&__text {
		margin-top: 16px;
		font-weight: 500;
		font-size: 14px;
		line-height: 24px;
	}

	&__image {
		flex: 1 1 49%;
		height: 100%;
		object-fit: contain;

		//MEDIA - 1300
		@media screen and (max-width: 1300px) {
			flex: 0 0 auto;
			max-width: 80%;
			margin-left: auto;
			margin-right: auto;
		}

		//!MEDIA
		//MEDIA - 767
		@media screen and (max-width: 767px) {
			max-width: unset;
			width: 100%;
		}

		//!MEDIA
	}
}

//!STYLES-SECTION



//STYLES-SECTION - icons

.icons {
	padding: 32px 0;
	background-color: white;

	& .section__title {
		margin-bottom: 32px;
	}

	&__inner {
		display: flex;
		justify-content: space-between;
		position: relative;

		@media screen and (max-width: 650px) {
			flex-direction: column;
			justify-content: center;
			align-items: center;
		}

		& .parallax-item {
			z-index: 0;

			&-1 {
				left: -220px;
				bottom: -70%;
			}

			&-2 {
				right: -220px;
				bottom: -175%;
			}
		}
	}

	&__item {
		max-width: 360px;
		text-align: center;
		min-height: 265px;
		position: relative;
		z-index: 1;
		width: 33%;

		@media screen and (max-width: 650px) {
			width: 100%;
		}
	}

	&__image-wrapper {
		border-radius: 50%;
		width: 144px;
		height: 144px;
		padding: 26px;
		background: linear-gradient(10.99deg,
				rgba(217, 190, 70, 0.75) 7.57%,
				rgba(244, 214, 76, 0.75) 96.86%);
		box-shadow: inset 0px 4px 20px rgba(0, 0, 0, 0.25);
		margin: 0 auto;
		position: relative;
		border: 5px solid #fcfcfc;
		transition: all 0.7s;

		& img {
			width: 100%;
			object-fit: contain;
			transition: all 0.7s;
		}

		&:hover {
			border: 10px solid #fff;
			background: linear-gradient(10.99deg,
					rgb(217, 190, 70) 7.57%,
					rgb(244, 214, 76) 96.86%);
			box-shadow: 0px 4px 30px rgba(159, 162, 26, 0.25),
				inset 0px 4px 20px rgba(0, 0, 0, 0.25);
			width: 194px;
			height: 194px;

			& img {
				transform: scale(1.6);
			}
		}
	}

	&__social {
		margin: 0 auto;

		&-list {
			list-style: none;
			text-align: center;
			margin-bottom: 24px;
		}

		&-item {
			display: inline-block;
			width: 44px;
			height: 44px;
			border: 1px solid $green;
			padding: 9px;
			border-radius: 50%;

			&+& {
				margin-left: 26px;
			}
		}
	}
}

//!STYLES-SECTION

//STYLES-SECTION - about

.about {
	padding: 48px;

	@media screen and (max-width: 560px) {
		padding: 48px 0;
	}

	& .section__title {
		margin-bottom: 16px;
	}

	& .section__text {
		font-size: 14px;
		line-height: 24px;
	}
}

//!STYLES-SECTION



//STYLES-SECTION - howto

.howto {
	margin-top: 48px;

	@media screen and (max-width: 768px) {
		margin-top: 258px;
	}

	& .container {
		@media screen and (max-width: 768px) {
			padding: 0;
		}
	}

	&__item {
		padding-top: 32px;
		padding-bottom: 32px;
		background-color: #fff;
		max-width: 935px;
		width: 60%;
		position: relative;
		margin-bottom: 48px;

		@media screen and (max-width: 768px) {
			width: 100%;
			max-width: unset;
			padding-bottom: 100px;
		}

		& .parallax-item-3 {
			left: -120px;
			width: 20%;
		}

		&-img {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			background: linear-gradient(10.99deg, #d9be46 7.57%, #f4d64c 96.86%);
			box-shadow: 0px 4px 30px rgba(159, 162, 26, 0.25),
				inset 0px 4px 20px rgba(0, 0, 0, 0.25);
			border-radius: 50%;
			height: 256px;
			width: 256px;
			border: 10px solid white;

			@media screen and (max-width: 768px) {
				top: 0;
				left: 50%;
				transform: translate(-50%, -70%);
			}

			& img {
				object-fit: contain;
				height: 100%;
				position: absolute;
				right: 0;
				margin: 0 auto;
				bottom: 30px;
			}
		}

		&-title {
			font-family: Ubuntu;
			font-style: normal;
			font-weight: bold;
			font-size: 18px;
			line-height: 32px;
			color: #384251;
			text-align: center;
			margin-bottom: 16px;
		}

		&-text {
			max-width: 650px;
			font-family: Inter;
			font-style: normal;
			font-weight: 500;
			font-size: 14px;
			line-height: 24px;

			a {
				color: #384251;
				text-decoration: underline;
			}

			strong {
				font-family: Inter;
				font-style: normal;
				font-weight: 600;
				font-size: 12px;
				line-height: 24px;
				color: #384251;
				margin-top: 16px;
			}
		}

		&--top {
			border-radius: 30px 0 0 30px;
			padding-left: 68px;
			padding-right: 185px;
			text-align: left;
			margin-right: auto;

			@media screen and (max-width: 768px) {
				border-radius: 0;
				padding: 100px 40px 32px;
				margin-left: 0;
				margin-right: 0;
				text-align: center;
			}

			& .howto__item-img {
				right: -128px;

				& img {
					left: 15px;
				}

				@media screen and (max-width: 768px) {
					right: unset;
				}
			}
		}

		&--bottom {
			border-radius: 0;
			padding-left: 185px;
			padding-right: 68px;
			text-align: right;
			margin-left: auto;

			@media screen and (max-width: 768px) {
				border-radius: 0;
				padding: 100px 40px 32px;
				margin-left: 0;
				margin-right: 0;
				margin-top: 250px;
				text-align: center;
			}

			& .howto__item-text {
				margin-left: auto;
			}

			& .howto__item-img {
				left: -128px;

				@media screen and (max-width: 768px) {
					left: 50%;
				}

				& img {
					left: -15px;
				}
			}

			.parallax-item {
				top: -40px;
			}
		}

		&-box {
			text-align: center;
		}

		&-wrapper {
			display: flex;
			justify-content: center;
			margin-top: 16px;
		}
	}

	&-inner {
		width: 120px;
		text-align: center;

		&__text {
			display: block;
			font-family: Inter;
			font-style: normal;
			font-weight: 600;
			font-size: 12px;
			line-height: 24px;
			color: $green;
			margin-top: 10px;
		}
	}

	&__title {
		margin: 48px 0;
	}

	&__list {
		display: flex;
		justify-content: space-between;
		counter-reset: howto-counter;

		//MEDIA - 767
		@media screen and (max-width: 767px) {
			flex-direction: column;
		}

		//!MEDIA
	}

	&__list-item {
		flex: 0 0 30%;
		text-align: center;
		position: relative;
		list-style: none;
		padding-top: 150px;
		margin-bottom: 60px;

		//MEDIA - 767
		@media screen and (max-width: 767px) {
			flex: 0 0 auto;
		}

		//!MEDIA

		&::before,
		&::after {
			content: "";
			position: absolute;
			height: 121px;
			width: 121px;
			top: 0;
			left: 50%;
			border-radius: 50%;
			transform: translateX(-50%);
			background-color: #ffffff;
			box-shadow: 24px 24px 60px rgba(232, 232, 232, 0.9),
				-24px -24px 48px rgba(255, 255, 255, 0.9),
				24px -24px 48px rgba(232, 232, 232, 0.2),
				-24px 24px 48px rgba(232, 232, 232, 0.2),
				inset -1px -1px 2px rgba(232, 232, 232, 0.5),
				inset 1px 1px 2px rgba(255, 255, 255, 0.3);
		}

		&::before {
			counter-increment: howto-counter;
			content: counter(howto-counter);
			text-align: center;
			padding: 17px;
			font-weight: 600;
			font-size: 72px;
			line-height: 87px;
			color: #8abc9e;
			z-index: 1;
		}

		&:hover::before {
			background: linear-gradient(175.6deg, rgb(0, 169, 66) -13.56%, rgb(32, 134, 72) 158.3%);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
	}

	&__list-item-title {
		font-weight: bold;
		font-size: 18px;
		line-height: 32px;
		color: $title;
	}

	&__list-item-text {
		margin-top: 16px;
		font-weight: 500;
		font-size: 14px;
		line-height: 24px;
	}
}

//!STYLES-SECTION



//STYLES-SECTION - bottom

.bottom {
	margin: 48px 0;
	position: relative;

	&__title {
		font-family: Ubuntu;
		font-style: normal;
		font-weight: bold;
		font-size: 12px;
		line-height: 32px;
		color: #727881;
	}

	.parallax-item {
		left: 30px;
		bottom: -30px;
	}

	&__text {
		font-family: Inter;
		font-style: normal;
		font-weight: 500;
		font-size: 11px;
		line-height: 18px;
		color: #727881;
	}

	&__form {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		width: 100%;
		max-width: 740px;
		margin: 48px auto 0;
	}

	&__form-label {
		flex: 0 0 49%;

		//MEDIA - 459
		@media screen and (max-width: 459px) {
			flex-basis: 100%;
		}

		//!MEDIA

		&--large {
			flex: 0 0 100%;
			height: 166px;
			position: relative;
		}

		&.text-input__wrapper {
			width: auto;
			max-width: unset;
			margin: 0;
			small{
				display: block;
				text-align: right;
			}

			.bottom__form-input,
			.bottom__form-text {
				transition: all 0.26s;
				box-shadow: 1px 1px 25px rgba(0, 0, 0, 0.05);
			}

