		&--file {
			position: absolute;
			bottom: 24px;
			right: 24px;
		}
	}


	&__input-file {
		height: 0;
		width: 0;
		display: block;
	}

	&__input-descr {
		font-size: 14px;
		line-height: 16px;
		text-decoration: underline;
		color: $green;
		padding-left: 25px;
		cursor: pointer;
		transition: all 0.26s;

		&::before {
			content: '';
			position: absolute;
			height: 16px;
			width: 16px;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			background-size: contain;
			background-position: center;
			background-repeat: no-repeat;
			background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13 13H15V14H13V16H12V14H10V13H12V11H13V13ZM3 1V13H9V14H2V0H8.71094L13 4.28906V10H12V5H8V1H3ZM9 1.71094V4H11.2891L9 1.71094Z' fill='%23208648'/%3E%3C/svg%3E%0A");
		}

		&:hover {
			text-decoration: none;
		}
	}
}

//!STYLES-SECTION



//STYLES-SECTION - footer

.footer {
