//!STYLES-SECTION



//STYLES-SECTION - modal

.modal {
	&-overlay {
		height: 100vh;
		width: 100vw;
		background-color: #00000075;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 11;
		display: none;
	}

	&-wrapper {
		min-height: 880px;
		height: auto;
		width: 100%;
		max-width: 994px;
		border-radius: 5px;
		position: absolute;
		left: 0;
		right: 0;
		margin: 0 auto;
		z-index: 12;
		top: calc((100vh - 880px) / 2);
		bottom: calc((100vh - 880px) / 2);
		background-color: #ffffff;
		padding: 20px;
		overflow: auto;

		@media screen and (max-height: 800px) {
			min-height: auto;
			height: 100vh;
			top: 0;
			bottom: 0;
			padding: 20px 20px 100px;
		}

		@media screen and (max-height: 800px) and (max-width: 994px) {
			border-radius: 0;
		}
	}

	&-close {
		border: none;
		box-shadow: none;
		position: absolute;
		right: 35px;
		top: 35px;
		height: 12px;
		width: 12px;
		background-color: #fff;
		transform-origin: right center;
		transition: all 0.3s;

		&:hover,
		&:focus {
			transform: scale(1.1);

			& span {
				box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
				background-color: $green;
			}
		}

		& span {
			position: absolute;
			border-radius: 5px;
			width: 100%;
			height: 2px;
			background-color: $greenGray;
			transition: all 0.3s;
			left: 0;
		}

		&__line1 {
			transform: rotate(-45deg);
		}

		&__line2 {
			transform: rotate(45deg);
		}
	}

	&-steps {
		text-align: center;
		font-family: Ubuntu;
		font-style: normal;
		font-weight: normal;
		font-size: 13px;
		line-height: 16px;
		color: $lightGreen;
		margin: 16px auto;

		&__current {
			font-size: 24px;
			font-weight: 700;
			margin: 0 5px;
		}

		&__last {
			color: $subGrey;
		}
	}

	&-form {
		margin: 0 auto;
		max-width: 760px;

		& .green-btn svg {
			height: 16px;
			width: 16px;
			margin-right: 6px;
		}
	}

	&-inputs {
		display: flex;
		flex-wrap: wrap;
		margin-left: -20px;
		margin-top: 48px;

		@media screen and (max-width: 800px) {
			flex-wrap: nowrap;
			flex-direction: column;
			margin: 48px auto 0;
		}
	}
}

.text-input__wrapper {
	width: 100%;
	max-width: 360px;
	margin-left: 20px;

	@media screen and (max-width: 800px) {
		margin: 0 auto;
	}

