.rules-checkbox {
	text-align: center;
	width: intrinsic;
	width: fit-content;
	display: block;
	margin: 32px auto 19px;
	position: relative;
	padding-top: 40px;

	& input {
		position: absolute;
		z-index: -1;
		opacity: 0;
		top: -40px;
		left: 0;
		right: 0;
		margin: 0 auto;
	}

	.rules-text {
		position: relative;
		display: block;
		cursor: pointer;
		font-weight: 300;
		font-size: 14px;
		line-height: 16px;
	}

	.rules-text:before {
		content: "";
		position: absolute;
		top: -40px;
		left: 0;
		right: 0;
		margin: 0 auto;
		width: 24px;
		height: 24px;
		background: #ffffff;
		border: 1px solid #ebedeb;
		box-shadow: 0px 7px 64px rgba(0, 0, 0, 0.07);
		border-radius: 6px;
		transition: 0.5s;
	}

	input:checked+.rules-text:after {
		content: "";
		top: -40px;
		left: 0;
		right: 0;
		margin: 0 auto;
		position: absolute;
		background-image: url(../img/Check.svg);
		background-repeat: no-repeat;
		background-position: center;
		width: 24px;
		height: 24px;
	}

	input:checked+.rules-text:before {
		border: none;
		box-shadow: 0px 7px 20px rgba(0, 0, 0, 0.07);
	}

	input:focus+.rules-text:before {
		box-shadow: 0px 7px 7px rgba(32, 134, 72, 0.5);
	}
}

.rules-link,
.rules-link:visited {
	color: $lightGreen;
	font-weight: 300;
	font-size: 14px;
	line-height: 16px;
	text-decoration: underline;
	transition: all 0.5s;

	&:hover,
	&:focus {
		color: $green;
	}
}

.green-btn.disabled {
	border: 1px solid #208648;
	box-sizing: border-box;
	border-radius: 30px;
	background: white;
	box-shadow: none;

	.green-btn__text {
		color: #208648;
	}

	svg path {
		fill: #208648;
	}
}

.fop {
	display: flex;
	width: 100%;
	justify-content: space-between;
	align-items: stretch;

	//MEDIA - 800
	@media screen and (max-width: 800px) {
		flex-direction: column;
	}

	//!MEDIA

	&__input-wrapper {
		flex: 1 1 50%;
		margin-left: 20px;
		margin-right: 0;
		width: auto;
		max-width: unset;
		padding: 0;
		transition: all 0.26s;

		//MEDIA - 800
		@media screen and (max-width: 800px) {
			flex: 0 0 auto;
			max-width: 360px;
			width: 100%;
		}

		//!MEDIA
	}

	&__check {
		display: flex;
		align-items: center;
		justify-content: center;

		//MEDIA - 800
		@media screen and (max-width: 800px) {
			margin-bottom: 32px;
		}

		//!MEDIA
	}

	&__text {
		display: none;
	}

	& &__check-text {
		display: inline;

		&::before,
		&::after {
			top: 50%;
			transform: translateY(-50%);
			left: -30px;
			right: auto;
		}
	}

	& &__input-wrapper input:checked+.fop__check-text::after {
		top: 50%;
		transform: translateY(-50%);
		left: -30px;
		right: auto;
	}
}

//!STYLES-SECTION



//STYLES-SECTION - top-button

.button-top {
	position: fixed;
	right: 50px;
	bottom: 50px;
	animation: bounce;
	animation-duration: 1s;
	animation-timing-function: ease-in-out;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	visibility: collapse;
	cursor: pointer;
	border-radius: 50%;
	overflow: hidden;
	transition: all 0.5s;
	box-shadow: 0px 4px 4px rgba(244, 214, 76, 0.2);
	z-index: 20;
	opacity: 0;
	height: 48px;
	width: 48px;
	background-color: #ffffff;
	border: 2px solid #578459;
	font-size: 0;
	background-image: url(../img/button_top.svg);
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat;

	@media screen and (max-width: 650px) {
		right: 25px;
		bottom: 25px;
		height: 32px;
		width: 32px;
	}

	&:hover {
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
	}

	&--visible {
		visibility: visible;
		opacity: 1;
	}
}

@keyframes bounce {
	form {
		transform: scale(0.9);
	}

	to {
		transform: scale(1.1);
	}
}

